* {
  box-sizing: border-box;
}

@font-face {
  font-family: "Avenir";
  src: url(./fonts/Avenir-Book.ttf);
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: "Avenir";
  src: url(./fonts/Avenir-Heavy.ttf);
  font-style: normal;
  font-weight: 900;
}

.search-form {
  margin-top: 20px;
}

.input {
  display: block;
  width: 100%;
  height: 70px;
  line-height: 70px;
  padding-left: 62px;
  font-family: Avenir, sans-serif;
  font-weight: 300;
  font-size: 16px;
  color: #3d4e61;
  background-color: #fff;
  background-image: url(./images/Search.svg);
  background-repeat: no-repeat;
  background-position: left 26px center;
  background-size: 19px;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
}

.input::placeholder {
  color: #3d4e61;
}

.input:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.input:focus {
  font-weight: 900;
  background-image: url(./images/Search.svg), linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  background-position: left 26px center, left top;
  background-size: 19px, 100%;
  outline: none;
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  box-shadow: none;
}

.input--small {
  height: 42px;
  padding-left: 33px;
  line-height: 42px;
  font-size: 14px;
  background-position: left 13px center;
  background-size: 11px;
}

.input--small:focus {
  background-position: left 13px center, left top;
  background-size: 11px, 100%;
}
